<template>
  <v-card color="#f1f1f1" flat tile>
    <v-container fluid>
      <v-row>
        <!-- LEFT SIDE: KUIS KETENTUAN LIST -->
        <v-col class="quiz-history-list">
          <v-container>
            <v-row>
              <v-data-iterator
                :items="quizList"
                :options.sync="options"
                @pagination="updatePagination"
                hide-default-footer
              >
                <template v-slot:default="props">
                  <v-col cols="12" v-for="item in props.items" :key="item.title">
                    <v-card :key="item.title" raised style="border-radius:15px;">
                      <QuizHistoryListCard
                        :title="item.name"
                        :score="item.score ? item.score : '0'"
                        :status="item.status"
                        :selectedCard="selectedCard ? selectedCard.name : ''"
                        :id="item.id"
                        @cardSelect="cardSelect"
                      />
                    </v-card>
                  </v-col>
                </template>
                <template v-slot:header>
                  <v-data-footer :pagination.sync="pagination" :options.sync="options">
                    <template v-slot:page-text>{{ options.page }} of {{ numberOfPages }}</template>
                  </v-data-footer>
                </template>
                <template v-slot:footer>
                  <v-data-footer :pagination.sync="pagination" :options.sync="options">
                    <template v-slot:page-text>{{ options.page }} of {{ numberOfPages }}</template>
                  </v-data-footer>
                </template>
              </v-data-iterator>
            </v-row>
          </v-container>
        </v-col>

        <!-- RIGHT SIDE: Kuis Ketentuan Detail Card -->
        <v-col>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-card raised style="border-radius: 15px; padding-left: 0.5em">
                  <QuizDetailCard v-if="selectedCard" :item="selectedCard" type="quizHistory" />
                </v-card>
              </v-col>
            </v-row>
          </v-container>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>

<script>
import QuizDetailCard from "@/components/QuizDetailCard.vue";
import QuizHistoryListCard from "@/components/QuizHistoryListCard.vue";
import ApiClient from "@/client/index.js";

// const PLACEHOLDER_DESCRIPTION =
//   "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do \
//   eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad \
//   minim veniam, quis nostrud exercitation ullamco laboris nisi ut \
//   aliquip ex ea commodo consequat.";

export default {
  components: {
    QuizDetailCard,
    QuizHistoryListCard
  },
  methods: {
    cardSelect(title) {
      const selfSelect =
        !!this.selectedCard && !!(this.selectedCard.name === title);
      if (selfSelect) {
        this.selectedCard = null;
      } else {
        this.selectedCard = this.quizList.find(item => item.name === title);
      }
    },
    loadUserQuizz() {
      ApiClient.quizzHistory(this.options.page).then(response => {
        this.quizList = response.data;
        this.totalItems = response.total;
        this.pagination.itemsLength = response.total;
      });
    },
    updatePagination(e) {
      this.pagination = e;
    }
  },
  watch: {
    options: {
      handler() {
        this.loadUserQuizz();
      }
    }
  },
  computed: {
    numberOfPages() {
      return Math.ceil(
        this.pagination.itemsLength / this.pagination.itemsPerPage
      );
    }
  },
  data() {
    return {
      selectedCard: null,
      quizList: [],
      totalItems: 0,
      pagination: {
        page: 1,
        itemsPerPage: 10,
        pageStart: 1,
        itemsLength: 0,
        pageCount: 1,
        pageStop: 10
      },
      options: {
        page: 1,
        itemsPerPage: 10
      }
    };
  },
  mounted() {
    this.loadUserQuizz();
  }
};
</script>

<style lang="scss" scoped>
.quiz-history-list {
  max-height: 75vh;
  overflow: auto;
}
</style>
