<template>
  <v-container fluid @click="handleClick" :class="[selected ? 'selected' : 'normal']">
    <v-row dense class="d-flex">
      <v-col>
        <v-row fluid dense>
          <v-col>
            <h2>{{ title }}</h2>
          </v-col>
        </v-row>
        <v-row fluid dense>
          <v-col class="card-section-left-bottom">
            <v-icon>mdi-document-outline</v-icon>
            Skor: {{ score }}
          </v-col>
        </v-row>
      </v-col>
      <v-col class="d-flex flex-align-center" cols="2">
        <v-chip color="#3ea48f" class="align-self-center" style="color: white; min-width: 75px">{{ status }}</v-chip>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  props: {
    title: String,
    score: String,
    status: String,
    selectedCard: String,
    id: Number
  },
  computed: {
    selected() {
      return this.title === this.selectedCard;
    }
  },
  methods: {
    handleClick() {
      this.$emit("cardSelect", this.title);
    }
  }
};
</script>

<style lang="scss" scoped>
.normal {
  border: 3px solid rgba(0, 0, 0, 0);
}
.selected {
  border: 3px solid #3ea48f;
  background: rgba(62, 164, 143, 0.3);
}

.card-section-left-bottom {
  padding-right: 0;
}
</style>
